import { _decorator, Component, Collider2D, Contact2DType, IPhysics2DContact, PhysicsSystem2D } from 'cc';
import { enemyManger } from './enemyManger';
const { ccclass, property } = _decorator;

@ccclass('Bullet')
export class Bullet extends Component {
    @property
    speed: number = 50;
    start() {
    }

    update(deltaTime: number) {
        let bulletY = this.node.position.y;
        bulletY += this.speed * deltaTime;
        this.node.setPosition(this.node.position.x, bulletY, 0);
        if (bulletY > 200) {
            this.node.destroy();
        }
    }
    die() {
        this.node.destroy();
    }
}

